.about-section {
  height: 100vh;
  background-color: #065a60;
}

.about-container {
  width: 59.25rem;
  margin: auto;
  padding-top: 12.5rem;
}

.about-heading {
  text-align: left;
}

.about-description {
  font-size: 1.5rem;
  font-family: Raleway;
  color: #fff;
  margin: 1rem 0;
}

.about-link {
  font-size: 1.125rem;
  font-family: Raleway;
  text-decoration: none;
  color: #ffffff60;
  transition: all 0.15s ease-in;
}

.about-link:hover {
  color: #4bbe6b;
}

@media only screen and (max-device-width: 1280px){
    .about-container{
        padding-top: 4rem;
    }
}

@media only screen and (max-device-width: 980px) {
  .about-container {
    width: 40rem;
    padding-top: 3rem;
  }
}

@media only screen and (max-device-width: 750px) {
  .about-container {
    width: 40rem;
    padding-top: 8rem;
  }
}

@media only screen and (max-device-width: 450px) {
    .about-container {
      width: 20rem;
      padding-top: 8rem;
    }

    .about-heading{
        font-size: 3rem;
    }

    .about-description{
        font-size: 1.25rem !important;
    }
  }
