.contactSection {
  width: 100%;
  height: 100vh;
  background-color: #fff;
  font-family: Raleway;
}

.contact-container {
  width: 51.938rem;
  margin: auto;
  padding-top: 13.3rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.contact-heading {
  color: #616161;
  font-family: Raleway;
  font-weight: 700;
  font-size: 3rem;
  text-align: left;
}

.contact-container form {
  width: 21.875rem;
  height: 24rem;
  margin-top: 3.125rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contact-container form div {
  display: flex;
  flex-direction: column;
}

.contact-container form label {
  font-family: Raleway;
  font-size: 0.875rem;
  color: #616161;
}

.contact-container form input {
  border-radius: 5px;
  border: 1px solid #eeeeee;
  outline: none;
  height: 1.875rem;
  font-family: Raleway;
  padding: 0.25rem;
}

.contact-container form textarea {
  height: 8.75rem;
  resize: none;
  padding: 0.25rem;
  border-radius: 5px;
  border: 1px solid #eeeeee;
  outline: none;
  font-family: Raleway;
}

.contact-container form button {
  width: 6.25rem;
  height: 1.875rem;
  border-radius: 1.25rem;
  border: none;
  outline: none;
  background-color: #3491ff;
  color: #fff;
}

.contact-description {
  height: 22rem;
  width: 26.563rem;
  margin: 7.5rem 3.5rem;
  display: flex;
  flex-direction: column;
}

.contact-descPersonal {
  height: 18.25rem;
}

.contact-PersonalContainer {
  margin-top: 3.125rem;
}

.contactPersonal {
  display: flex;
  flex-direction: row;
  line-height: 40px;
}

.contactPersonal a {
  margin-left: 1.25rem;
  text-decoration: none;
  color: #000;
}

.contactPersonal a:hover {
  text-decoration: underline;
}

.contact-socialMediaContainer {
  width: 4.625rem;
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-device-width: 1100px) {
  .contact-container {
    width: 41.938rem;
    padding-top: 15rem;
    flex-direction: column;
  }

  .contact-heading {
    text-align: center;
  }

  .contact-container form {
    width: 22.875rem;
    height: 20rem;
    margin-top: 3.125rem !important;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: auto;
  }

  .contact-description {
    margin: auto;
    margin-top: 5rem;
  }

  .contact-descPersonal {
    margin: auto;
    width: 20rem;
  }

  .contact-socialMediaContainer {
    margin: auto;
  }
}

@media only screen and (max-device-width: 450px) {
  .contact-container {
    width: 19.938rem;
    padding-top: 5rem;
    flex-direction: column;
    padding-bottom: 7rem
  }

  .contact-heading {
    text-align: center;
  }

  .contact-container form {
    width: 100%;
    height: 20rem;
    margin-top: 3.125rem !important;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: auto;
  }

  .contact-description {
    width: 100%;
    margin: auto;
    margin-top: 5rem;
  }

  .contact-descPersonal {
    margin: auto;
    width: 100%;
  }

  .contact-descPersonal p {
    width: 90%;
  }

  .contact-socialMediaContainer {
    margin: auto;
  }
}
