* {
  margin: 0;
  padding: 0;
}

.projectContainer {
  width: 58.75rem;
  display: flex;
  flex-direction: row;
  margin-top: 12.875rem;
  color: #fff;
  font-family: Raleway;
}

.projectContentContainer {
  height: 20rem;
  margin-top: 1.25rem;
  display: flex;
  flex-direction: column;
  text-align: left;
  background-color: #fff;
}

.projectHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.875rem;
  background-color: #ef6351;
}

.projectHeading {
  width: 10.5rem;
  margin-top: 0.6rem;
  font-weight: 500;
}

.projectHeading a {
  text-decoration: none;
  color: #fff;
}

.projectHeading a:hover {
  text-decoration: underline;
}

.projectTagContainer {
  width: 20rem;
  display: flex;
  flex-direction: row;
}

.projectTag {
  margin: 0 15% 0 0;
  padding: 0.875rem;
  background-color: #00000025;
  font-size: 0.75rem;
  border-radius: 1.563rem;
}

.projectDescription {
  padding: 2%;
  font-size: 1rem;
  color: #000;
}

.projectImg {
  margin-top: 0.5rem;
  width: 42.875rem;
  height: 21.25rem;
}

@media only screen and (max-device-width: 1100px) {
  .projectContainer {
    flex-direction: column;
    width: 40rem;
    margin: 2.875rem auto;
  }

  .projectHeading {
    font-size: 1rem;
  }

  .projectContentContainer {
    width: 40rem;
    margin: auto;
  }

  .projectImg {
    width: 42.875rem;
    height: 21.25rem;
    margin-left: -1.5rem;
    margin-top: 0;
  }
  
}

@media only screen and (max-device-width: 450px) {
  .projectContainer {
    flex-direction: column;
    width: 22rem;
    height: 20rem;
    margin: 2.875rem auto;
  }

  .projectHeader {
    flex-direction: column;
    height: 5rem;
    justify-content: space-between;
  }

  .projectHeading {
    font-size: 1.25rem;
    width: 22rem;
    margin-bottom: 0.75rem;
  }

  .projectContentContainer {
    width: 22rem;
    height: 30.903rem;
    margin: auto;
  }

  .projectImg {
    width: 22rem;
    height: 174.45px;
    margin-left: -1.5rem;
    margin: auto;
  }
}
