.home-section {
  background: #361999;
  background: linear-gradient(
    -45deg,
    rgba(54, 25, 153, 1) 0%,
    rgba(71, 23, 233, 1) 100%
  );
  width: 100%;
  height: 100vh;
}

.home-container {
  width: 65rem;
  margin: auto;
  padding-top: 12rem;
}

.home-heading {
  text-align: left;
}

@media only screen and (max-device-width: 1400px) {
  .home-heading {
    font-size: 4.5rem;
  }

  .home-headingDesc {
    font-size: 2rem !important;
  }

  .home-container {
    width: 44rem;
    margin: auto;
    padding-top: 20rem;
  }
}

@media only screen and (max-device-width: 1400px) and (max-device-height: 925px) {
  .home-container {
    padding-top: 10rem;
  }
}

@media only screen and (max-device-width: 750px) {
  .home-container {
    width: 38rem;
  }

  .home-heading {
    font-size: 3.5rem;
  }

  .home-headingDesc {
    font-size: 1.7rem !important;
  }
}

@media only screen and (max-device-width: 450px) {

  .home-container{
    width: 20rem;
  }

  .home-heading{
    font-size: 2.3rem;
  }

  .home-headingDesc{
    font-size: 1.5rem !important;
  }

  .home-socialMedia{
    width: 5rem !important;
  }

  .home-socialMedia svg{
    width: 35px;
    height: 35px;
  }
}

.home-headingDesc {
  font-family: Raleway;
  color: #fff;
  font-weight: 400;
  font-size: 3rem;
}

.home-socialMedia {
  width: 7.5rem;
  display: flex;
  margin-top: 2rem;
  flex-direction: row;
  justify-content: space-between;
}

.home-socialMedia a {
  transition: all 0.15s ease-in-out;
}

.home-socialMedia a:hover {
  transform: translate(-3px, -10px);
}
