.navbar-nav {
  padding: 2rem;
  position: fixed;
  width: 100%;
  z-index: 300;
}

.navbar-ul {
  width: 90%;
  height: 10.75rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.navbar-li {
  height: 2.15rem;
  list-style: none;
}

.link {
  font-family: Raleway;
  text-decoration: none;
  color: #ffffff60;
  line-height: 2.15rem;
  transition: all 0.05s ease-in;
}

.link:hover {
  font-size: 1.5rem;
  color: #ffffff;
}

.navbar-active {
  font-weight: 600;
  color: #ffffff;
}

.navbar-active .link {
  font-size: 1.5rem;
}

.nav-black {
  color: #000;
}

.nav-black:hover {
  font-size: 1.5rem;
  color: #000000;
}

.nav-davidsteenmann .link {
  color: #78fff1;
  font-weight: 600;
}

.nav-about .link {
  color: #4bbe6b;
  font-weight: 600;
}

.nav-skills .link {
  color: #f0db4f;
  font-weight: 600;
}

.nav-projects .link {
  color: #ef6351;
  font-weight: 600;
}

.nav-contact .link {
  color: #3491ff;
  font-weight: 600;
}

@media only screen and (max-device-width: 1200px) {
  .navbar-nav {
    width: 100%;
    height: 2rem;
    bottom: 0;
  }

  .navbar-ul {
    flex-direction: row;
    justify-content: space-evenly;
    height: 1rem;
  }

  .link {
    font-family: Raleway;
    text-decoration: none;
    color: #ffffff60;
    line-height: 2.15rem;
    transition: all 0.05s ease-in;
  }

  .link:hover {
    font-size: 1.5rem;
    color: #ffffff;
  }

  .navbar-active {
    font-weight: 600;
    color: #FEFEFE;
  }

  .navbar-active .link {
    font-size: 1.5rem;
  }

  .navbar-davidsteenmann {
    background-color: #3f37c9;
  }

  .navbar-about {
    background-color: #1b747a;
  }

  .navbar-skills {
    background-color: #242424;
  }

  .navbar-projects {
    background-color: #fdfdfd;
  }

  .navbar-contact{
      background-color: #EEEEEE;
  }

  .nav-black {
    color: #000;
  }

  .nav-black:hover {
    font-size: 1.5rem;
    color: #000000;
  }

  .nav-davidsteenmann .link {
    color: #78fff1;
    font-weight: 600;
  }

  .nav-about .link {
    color: #4bbe6b;
    font-weight: 600;
  }

  .nav-skills .link {
    color: #f0db4f;
    font-weight: 600;
  }

  .nav-projects .link {
    color: #ef6351;
    font-weight: 600;
  }

  .nav-contact .link {
    color: #3491ff;
    font-weight: 600;
  }
}

@media only screen and (max-device-width: 450px) {
    .navbar-nav{
        margin-left: -1rem;
    }

    .link{
        font-size: 0.75rem;
    }

    .navbar-active .link{
        font-size: 1rem;
    }
}
